<template>
  <v-container class="grey lighten-5">
    <v-row 
        class="justify-center"
        v-for="(row, key) in all_rows"
        v-bind:key="key"
    >
        <v-col
            cols="12"
            :sm="item.size"
            class="ma-0 pa-1"
            v-for="item in row" 
            v-bind:key="item.text"
        >
            <v-btn 
            block 
            x-large
            @click="on_click(item.text)"
            >{{ item.text }}</v-btn>
      </v-col>
    </v-row>
  </v-container>
</template>

<script>
//import Router from 'vue-router'
export default {
    name: "keypad_full",
    data: () => ({
        errors: [],
        all_rows : {
            row_1 :[
                {text:"1",size:1},
                {text:"2",size:1},
                {text:"3",size:1},
                {text:"4",size:1},
                {text:"5",size:1},
                {text:"6",size:1},
                {text:"7",size:1},
                {text:"8",size:1},
                {text:"9",size:1},
                {text:"BackSpace",size:2},
            ],
            row_2 :[
                {text:"q",size:1},
                {text:"w",size:1},
                {text:"e",size:1},
                {text:"r",size:1},
                {text:"t",size:1},
                {text:"y",size:1},
                {text:"u",size:1},
                {text:"i",size:1},
                {text:"o",size:1},
                {text:"p",size:1},
            ],
            row_3 :[
                {text:"a",size:1},
                {text:"s",size:1},
                {text:"d",size:1},
                {text:"f",size:1},
                {text:"g",size:1},
                {text:"h",size:1},
                {text:"j",size:1},
                {text:"k",size:1},
                {text:"l",size:1},
                {text:"Enter",size:2},
            ],
            row_4 :[
                {text:"z",size:1},
                {text:"x",size:1},
                {text:"c",size:1},
                {text:"v",size:1},
                {text:"b",size:1},
                {text:"n",size:1},
                {text:"m",size:1},
                {text:".",size:1},
                {text:"-",size:1},
                {text:"Clear",size:1},
            ],
            row_5 :[
                {text:"Space",size:5},
            ],
        },
        component_name : "keypad_full",
        btn_class : "ma-0 pa-1",
    }),
    props: {

    },

    computed: {

    },
    created (){
        //actions.users.fetchUsersFromApi()

    },
    methods: {
        on_click(val){
            switch(val)
            {
                case "Clear":case "BackSpace":case "Space":case "Enter":
                    //功能键
                   this.$emit("FeedbackFromKeyboard",val.toLowerCase());
                break;
                default:
                    this.$emit("FeedbackFromKeyboard",val);
                break;
            }
        },
    },
}
</script>

